// animations for home
// switching text animation
@keyframes swoosh {
  0% {
    transform: translateY(0);
    opacity: 1;
  }
  8% {
    opacity: 1;
  }

  30% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  70% {
    opacity: 0;
  }
  100% {
    transform: translateY(-145px);
    opacity: 1;
  }
}

// animation for text shadow
@keyframes text-shadow {
  from {
    text-shadow: none;
  }
  to {
    text-shadow: 7px 7px 11px $light-blue-green, 10px 10px 3px $light-blue-green;
  }
}

// animation for abstract lines on the left
@keyframes move {
  0% {
    transform: scaleX(0);
  }

  100% {
    transform: scaleX(1.4);
    opacity: 0;
  }
}
// animation for circles spinning
@keyframes spin {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}

// animations for about page
// animation for list of what I love about coding
@keyframes drop {
  0% {
    opacity: 0;
    transform: translateY(50px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

